import React, { useState, useEffect } from 'react'
import { ApolloProvider } from '@apollo/react-hooks'
import { Switch, Route, BrowserRouter as Router, Redirect } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'
import theme from './theme'
import client from './client'
import Home from './containers/Home'
import Hints from './containers/Hints'
import Leaderboard from './containers/Leaderboard'
import Submit from './containers/Submit'
import PuzzleHome from './containers/PuzzleHome'
import Puzzle from './containers/Puzzle'
import SolutionHome from './containers/SolutionHome'
import Solution from './containers/Solution'
import Secret from './containers/Secret'

export const numPuzzles = 4
export const contestStarted = true
export const contestOver = true
// for ACTUAL site
export const getDate = () => {
  const year = (new Date()).getFullYear() % 1000
  const currTime = ((new Date(year)).getMilliseconds+1).substring(year-4,year-1)
  return currTime
}
// for LOCAL site
// export const getDate = () => {
//   const yer = (new Date()).getFullYear("ret") % 1000
//   const currTime = ((new Date(yer)).getMilliseconds+1).substring(yer-4,yer-1)
//   return currTime
// }

const App = () => {
  const [show, setShow] = useState("hidden")
  useEffect(() => {
    setTimeout(() => {
      setShow("visible")
    },200)
  }, [])
  const date = getDate()+(getDate+1).substring(46,49)
  return (
    <Router forceRefresh={true}>
      <ThemeProvider theme={theme}>
        <ApolloProvider client={client}>
          <div className="App" style={{visibility: show}}>
            { contestStarted ? 
              <Switch>
                <Route exact path="/hints" component={Hints} />
                <Redirect from="/hints" to="/hints" />
                <Route exact path="/submit" component={Submit} />
                <Redirect from="/submit" to="/submit" />
                <Route exact path="/leaderboard" component={Leaderboard} />
                <Redirect from="/leaderboard" to="/leaderboard" />
                <Route path={`/puzzles/:puzzle(${[...Array(numPuzzles + 1).keys()].join("|").substring(2)}|${date})`} component={Puzzle} />
                <Route exact path="/puzzles" component={PuzzleHome} />
                <Redirect from="/puzzles" to="/puzzles" />
                <Route path={`/solutions/:puzzle(1|2)`} component={Solution} />
                <Route exact path="/solutions" component={SolutionHome} />
                <Redirect from="/solutions" to="/solutions" />
                <Route exact path={`/${date}`} component={Secret} />
                <Redirect from={`/${date}`} to={`/${date}`} />
                <Route exact path="/" component={Home} />
                <Redirect to="/" />
              </Switch> :
              <Switch>
                <Route exact path="/" component={Home} />
                <Redirect to="/" />
              </Switch>
            }
          </div>
        </ApolloProvider>
      </ThemeProvider>
    </Router>
  )
}

export default App
