import React, { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import titles from './titles'
import { FullDiv } from './styles'
import { getDate } from '../../App'

const Solution = () => {
  const { puzzle } = useParams()
  const title = titles[puzzle-1]
  const date = getDate()+(getDate+1).substring(46,49)
  useEffect(() => {
    document.title = `${!!title ? title : date.charAt(0).toUpperCase()+date.slice(1)+" Solution"} | Harvard Puzzle Hunt`
  }, [])
  return (
    <FullDiv>
      <object data={`${title}.pdf`} type="application/pdf" width="100%" height="100%" />
    </FullDiv>
  )
}

export default Solution
