import React, { useState } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { USER_BY_ID } from '../graphql'
import {
  BackgroundDiv, BodyDiv, Title, Subtitle, StyledP, Subsubtitle, SubP, HiddenLink,
} from '../styles'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { decodeToken } from '../auth'
import { getDate } from '../../App'
import { contestStarted } from '../../App'

const Home = () => {
  const [user, setUser] = useState({})
  const { loading } = useQuery(USER_BY_ID, {
    variables: {
      id: decodeToken(localStorage.getItem('token')).id
    },
    onCompleted: ({ userById: user }) => {
      setUser(user)
    },
    skip: !localStorage.getItem('token'),
  })
  const date = getDate()+(getDate+1).substring(46,49)
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} loading={loading} onPage="Home" />
      <BodyDiv home={true}>
        <Title home={true}>Welcome to Harvard Puzzle Club's first Puzzle Hunt!</Title>
        <Subtitle>About Us</Subtitle>
        <StyledP>
          We've set out to start a club at Harvard centered entirely around <b>recreational (and sometimes slightly competitive) puzzle solving</b>.
          Currently, there really isn't a club at Harvard for the puzzle enthusiast community, and we
          really think there should be. A couple of us actually met through CS50 Puzzle Day, and we
          really loved how that event pulled together a huge community of puzzle-solvers... but sadly
          it only happens once a year! Harvard Puzzle Club aims to change that by hosting a puzzle hunt
          (tentatively) 4 times a year and being active year-round for all things puzzle-y. We'll have
          regular puzzle sessions for sharing puzzles we've written and challenging our friends to try
          to solve them. Some of them could end up in an actual hunt! If you like puzzles at all,
          please <b>join our mailing list below</b>-- we'll be applying for club status in the fall and any support
          can go a long way!
        </StyledP>
        <Subtitle>About the Contest</Subtitle>
        <StyledP>
          This summer, we're rolling out the first run of the Harvard Puzzle Hunt. This first round is
          a <b>beta version</b>, so any feedback about bugs, design suggestions, or anything in general is much
          appreciated (links below).
        </StyledP>
        <Subsubtitle>Registering</Subsubtitle>
        <SubP>
          You can register as an <b>individual or as a team of any size</b>, but keep in mind you'll have to
          share the prize money among your whole team. To simplify things, the website is configured
          such that registering a new team and logging in as a returning team use the same form,
          located under the <b>Submit page</b>. If you've ever used when2meet, the form works the same way.
          To register, simply enter your information, and make sure the rest of your team also knows
          the credentials. Then, when you return, you can enter the same information to log in. If you want us to
          be able to contact you should you win a prize, make sure to <b>fill in your email</b>!
        </SubP>
        <Subsubtitle>Puzzles</Subsubtitle>
        <SubP>
          <b>Puzzles can be solved in any order</b>, but some are harder than others.
          The length of the contest will be one month, but it is meant to be largely open-ended.
          However, we expect every puzzle to be solved before that deadline, so <b>the winner will be the
          first to solve <i>all</i> the puzzles</b>.
          Since team sizes are unlimited and you have a full month to solve these,
          one or more of the puzzles may take a pretty long time. We highly suggest splitting up grunt work
          among multiple team members if you're willing to split the prize.
          The leaderboard will hide your last solve in order to maintain some level
          of mystery throughout the hunt, and final standings won't be released until the hunt is over.
          Be on the look out for extra clues-- <b>there might be some {
            contestStarted ? <HiddenLink to={`/${date}`}>secret features...</HiddenLink> : "secret features..."
          } </b>
        </SubP>
        <Subsubtitle>Prizes</Subsubtitle>
        <SubP>
          The <b>overall winner will win the grand prize of $15</b>, and the <b>first solvers for each puzzle
          will each win mini prizes of $3 each as well</b>, but there's no limit on the number of 
          prizes a single team can win (prizes will get <b><i>much</i> bigger</b> once we get
          funding-- <b>join our mailing list</b> to help make that happen!). So be strategic about which puzzles you start with!
          The <b>top 25 teams will get first dibs for puzzle submissions for the next contest</b>, so if that's
          something you're interested in, this would be a great way to get your name in! If you want to
          redeem a prize, <b>make sure you've filled out your email</b> in the form so that we know who you are
          and have a way of contacting you.
        </SubP>
        <Subtitle>Interested in Joining?</Subtitle>
        <StyledP>
          If you enjoy the hunt and want to participate next time, please <b>join our mailing list below</b> so that you're
          up to date on all Harvard Puzzle Club news.
          If you want to be a part of the next contest <b>as a puzzle-<i>writer</i></b>, there's a place to indicate
          that on the form as well (remember you can get first dibs by making top 25!).
        </StyledP>
        <Title bottom={true}>Good Luck Puzzlers!</Title>
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default Home
